/* 전역 스타일 */
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background: linear-gradient(135deg, #0b0b1a 0%, #1a1a3e 50%, #0d0d2b 100%);
    background-attachment: fixed;
    color: #fff;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    min-height: 100vh;
}

a {
    text-decoration: none;
    color: inherit;
}

/* Home 페이지 */
.home-container {
    max-width: 1100px;
    margin: 0 auto;
    padding: 40px 20px;
}

.home-title {
    text-align: center;
    font-size: 2.4em;
    margin-bottom: 32px;
    background: linear-gradient(90deg, #61dafb, #a78bfa);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.loading {
    text-align: center;
    padding: 100px 0;
    font-size: 1.5em;
    color: #888;
}

.movies-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(220px, 1fr));
    gap: 24px;
}

/* Movie 카드 */
.movie-card {
    background: rgba(255, 255, 255, 0.05);
    border-radius: 16px;
    overflow: hidden;
    transition: transform 0.3s, box-shadow 0.3s;
    backdrop-filter: blur(10px);
    border: 1px solid rgba(255, 255, 255, 0.08);
}

.movie-card:hover {
    transform: translateY(-8px);
    box-shadow: 0 12px 40px rgba(97, 218, 251, 0.15);
}

.movie-card img {
    width: 100%;
    display: block;
}

.movie-card-body {
    padding: 16px;
}

.movie-card-title {
    font-size: 1.1em;
    font-weight: bold;
    color: #fff;
    margin-bottom: 8px;
    transition: color 0.2s;
}

.movie-card-title:hover {
    color: #61dafb;
}

.movie-card-summary {
    font-size: 0.85em;
    color: #aaa;
    line-height: 1.5;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
    margin-bottom: 10px;
}

.genre-list {
    list-style: none;
    display: flex;
    flex-wrap: wrap;
    gap: 6px;
}

.genre-tag {
    background: rgba(97, 218, 251, 0.15);
    color: #61dafb;
    padding: 3px 10px;
    border-radius: 12px;
    font-size: 11px;
}

/* Detail 페이지 */
.detail-page {
    max-width: 1100px;
    margin: 0 auto;
    padding: 40px 20px;
}

.detail-container {
    display: flex;
    gap: 32px;
}

.detail-poster {
    flex-shrink: 0;
}

.detail-poster img {
    width: 400px;
    border-radius: 16px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
}

.detail-info {
    flex: 1;
}

.detail-header {
    display: flex;
    align-items: baseline;
    gap: 14px;
    margin-bottom: 20px;
    flex-wrap: wrap;
}

.detail-title {
    font-size: 2em;
    margin: 0;
}

.detail-date {
    color: #888;
    font-size: 0.9em;
    white-space: nowrap;
}

.detail-rating {
    color: #f5c518;
    font-size: 0.9em;
    font-weight: bold;
    white-space: nowrap;
}

.detail-overview {
    line-height: 1.8;
    color: #ccc;
    font-size: 1.05em;
}

/* 뒤로가기 버튼 */
.back-btn {
    background: none;
    border: none;
    color: #666;
    font-size: 14px;
    cursor: pointer;
    padding: 4px 0;
    margin-bottom: 24px;
    display: inline-flex;
    align-items: center;
    gap: 4px;
    transition: color 0.2s;
}

.back-btn:hover {
    color: #61dafb;
}